.image-cards {
    display: flex;
    justify-content: space-around;
    margin-top: 20px;
  }
  
  .image-card {
    width: 350px;
    height: 200px;
    overflow: hidden;
    border-radius: 10px;
    transition: transform 0.3s ease-in-out;
    cursor: pointer;
  }
  
  .image-card img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .image-card:hover {
    transform: scale(1.1);
  }
  